import React from "react";
import Box from "@mui/material/Box";
import { useLocation, Link, Location } from "react-router-dom";
import {
  Avatar,
  Typography,
  useTheme,
} from "@mui/material";
import useMediaQuery from "@mui/material/useMediaQuery";
import Images from "../../assets";
import { useAppSelector } from "../../assets/hooks";

const profileList = [
  { path: "/profile/dashboard", name: "Dashboard", icon: <img src={Images.dashboard} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/personal", name: "Profile", icon: <img src={Images.personal} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/documents", name: "Documents", icon: <img src={Images.documents} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/applications", name: "My Products", icon: <img src={Images.products} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/recommendedCourses/Safe", name: "Recommendation", icon: <img src={Images.recommendations} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/shortlisted", name: "Favourites", icon: <img src={Images.wishlist} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/experts", name: "Experts", icon: <img src={Images.experts} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/orders", name: "My Orders", icon: <img src={Images.orders} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
  { path: "/profile/cart", name: "Cart", icon: <img src={Images.cart} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
];

interface SidebarContentProps {
  location: Location;
  profile: any; // Replace 'any' with your profile type if available
  isRecommendedCoursesActive: boolean;
  onClose?: () => void;
}

const SidebarContent = ({ location, profile, isRecommendedCoursesActive, onClose }: SidebarContentProps) => (
  <Box
    sx={{
      width: 260,
      minHeight: 600,
      bgcolor: '#fff',
      borderRadius: '20px',
      border: '1.5px solid #E6E6E6',
      px: 0,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      boxShadow: 'none',
    }}
  >
    {/* Profile Section */}
    <Box sx={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      mb: 2.5,
      width: '100%',
      bgcolor: '#EFF4F7',
      borderTopLeftRadius: '20px',
      borderTopRightRadius: '20px',
      pt: 2.5,
      pb: 2,
    }}>
      <Avatar
        src={profile.data?.displayPicSrc}
        alt="profile"
        sx={{ width: 54, height: 54, mb: 1.2 }}
      />
      <Typography
        color="#495052"
        sx={{ fontWeight: 700, fontSize: '1.13rem', mb: 0.2, textAlign: 'center', letterSpacing: 0.1 }}
      >
        {profile?.data?.firstName} {profile?.data?.lastName}
      </Typography>
      <Typography sx={{ color: '#5C6163', fontSize: '0.97rem', textAlign: 'center', fontWeight: 400 }}>
        {profile?.data?.email}
      </Typography>
    </Box>
    {/* Menu Items */}
    <Box sx={{ width: '90%', display: 'flex', flexDirection: 'column', gap: 0 }}>
      {profileList.map((item, idx) => {
        const isActive = (location.pathname === item.path) || (item.name === 'Recommendation' && isRecommendedCoursesActive && item.path.startsWith('/profile/recommendedCourses'));
        return (
          <React.Fragment key={item.name}>
            <Box
              component={Link}
              to={item.path}
              onClick={onClose}
              sx={{
                display: 'flex',
                alignItems: 'center',
                gap: 1.7,
                px: 3,
                py: 1.7,
                borderRadius: '12px',
                cursor: 'pointer',
                bgcolor: isActive ? '#70A6B2' : 'transparent',
                fontWeight: isActive ? 700 : 500,
                color: isActive ? '#ffff' : '#495052',
                transition: 'background 0.2s, color 0.2s',
                boxSizing: 'border-box',
                textDecoration: 'none',
                '&:hover': {
                  ...(isActive
                    ? { bgcolor: '#70A6B2', color: '#fff' }
                    : { bgcolor: '#EFF4F7', color: '#3B3F76' }),
                },
                textTransform: 'none',
                fontSize: '1.01rem',
                justifyContent: 'flex-start',
              }}
            >
              <Box
                sx={{
                  minWidth: 28,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  '& img': {
                    filter: isActive ? 'brightness(0) invert(1)' : 'none',
                    transition: 'filter 0.2s',
                  },
                }}
              >
                {item.icon}
              </Box>
              <Typography
                sx={{
                  fontWeight: isActive ? 700 : 500,
                  fontSize: '1.01rem',
                  color: 'inherit',
                  textTransform: 'capitalize',
                  ml: 1,
                }}
              >
                {item.name}
              </Typography>
            </Box>
            {idx !== profileList.length - 1 && (
              <Box sx={{ width: '80%', mx: 'auto', borderBottom: '1px solid #E6E6E6' }} />
            )}
          </React.Fragment>
        );
      })}
    </Box>
  </Box>
);

const ProfileSidebar = ({ mobileOnly = false, forDrawer = false, onClose }: { mobileOnly?: boolean, forDrawer?: boolean, onClose?: () => void }) => {
  const location = useLocation();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));
  const profile = useAppSelector((state) => state.sharedInfo);
  const isRecommendedCoursesActive = location.pathname.startsWith('/profile/recommendedCourses');

  // For mobile, return null unless it's for the drawer
  if (isMobile && !forDrawer) {
    return null;
  }
  
  // If it's for the drawer (mobile), return the sidebar content
  if (isMobile && forDrawer) {
    return <SidebarContent location={location} profile={profile} isRecommendedCoursesActive={isRecommendedCoursesActive} onClose={onClose} />;
  }

  // Desktop sidebar
  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'flex-start',
        width: 260,
        minHeight: 600,
        py: 2,
      }}
    >
      <SidebarContent location={location} profile={profile} isRecommendedCoursesActive={isRecommendedCoursesActive} />
    </Box>
  );
};

export default ProfileSidebar;
