import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Menu from "@mui/material/Menu";
import MenuIcon from "@mui/icons-material/Menu";
import Container from "@mui/material/Container";
import Avatar from "@mui/material/Avatar";
import MenuItem from "@mui/material/MenuItem";
import Images from "../../assets";
import { Backdrop, Button, List, ListItemButton, Stack, Drawer } from "@mui/material";
import { CustomButton } from "../../genericComponents/customButton";
import { useLocation, useNavigate } from "react-router-dom";
import { Event, FilterProps, exploreListInfo } from "../../types/types";
import { useAppDispatch, useAppSelector } from "../../assets/hooks";
import { setPopup } from "../../store/Slices/popupSlice";
import Person2Icon from "@mui/icons-material/Person2";
import axios from "axios";
import Search from "./search";
import { logout } from "../../assets/library";
import { AuthLogout } from "../../services";
// import BookmarkBorderIcon from "@mui/icons-material/BookmarkBorder";
import Explorepopup from "./explorepopup";
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import ConfirmDialog from "../../genericComponents/modalPopup/confirmDialog";
import { expandCardStyles, globalButton } from "../styles/customStyles";
import ExplorePop from "./explorePop";
import ProfileSidebar from "../../pages/profile/profileSidebar";
import Search2 from "./search2";


const Header = (props: FilterProps) => {
  const location = useLocation();
  // eslint-disable-next-line
  const [anchorElNav, setAnchorElNav] = React.useState<null | HTMLElement>(null);
  const [anchorElUser, setAnchorElUser] = React.useState<null | HTMLElement>(null);
  const [anchorElExplore, setAnchorElExplore] = React.useState<null | any>(null);
  const queryParams = new URLSearchParams(location.search);
  const [exploreopen,setExploreOpen] = React.useState(false)
  let homepage = queryParams.get("exp");
  let login = queryParams.get("loginp")

  React.useEffect(() => {
    if (homepage) {
      setAnchorElExplore(true);
    } else if (login) {
      dispatch(
        setPopup({
          show: true,
          data: {
            container: {
              name: "login",
              dimensions: {
                width: "800px",
                height: "70%",
                show: "none"
              },
            },
            type: "custom",
          },
        })
      );
    }
    // eslint-disable-next-line
  }, [])
  // eslint-disable-next-line


  const coursesListInfo = localStorage.getItem("coursesListInfo");
  const exploreHistory: exploreListInfo = coursesListInfo
    ? JSON.parse(coursesListInfo)
    : null;
  const [activePage, setActivePage] = React.useState<string | null>("Home");

  const pages = React.useMemo(() => [
    {
      title: "Home",
      link: "/",
      // icon: <HomeIcon />
    },
    {
      title: "Explore",
      link: `/explore?activeTab=${JSON.stringify(
        "courses"
      )}&FilterData=${JSON.stringify(
        exploreHistory?.appliedFilters ? exploreHistory.appliedFilters : []
      )}&search=${JSON.stringify(
        exploreHistory?.search ? exploreHistory.search : ""
      )}&page=${JSON.stringify(
        exploreHistory?.page ? exploreHistory.page : 1
      )}`,
      // icon: <TravelExploreIcon />,
      options: [
        {
          name: "Destinations",
          link: `/explore?activeTab=${JSON.stringify(
            "courses"
          )}&FilterData=${JSON.stringify([])}&page=${JSON.stringify(1)}&search=${JSON.stringify("")}`,
        },
        {
          name: "Universities",
          link: `/explore?activeTab=${JSON.stringify(
            "universities"
          )}&FilterData=${JSON.stringify([])}&page=${JSON.stringify(1)}&search=${JSON.stringify("")}`,
        },
        {
          name: "Courses",
          link: `/explore?activeTab=${JSON.stringify(
            "courses"
          )}&FilterData=${JSON.stringify([])}&page=${JSON.stringify(1)}&search=${JSON.stringify("")}`,
        },
      ]
    },
    {
      title: "AI Course Finder",
      link: "/startpage",
      // icon: activePage === "AI Course Finder" ? <img src={Images.activeai} alt="" style={{ width: "1.5rem" }} /> : <img src={Images.inactiveai} alt="" style={{ width: "1.5rem" }} />
    },
  ], [exploreHistory]);

  const settings = [
    { path: "/profile/dashboard", name: "Dashboard", icon: <img src={Images.dashboard} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
    { path: "/profile/personal", name: "Profile", icon: <img src={Images.personal} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
    { path: "/profile/documents", name: "Documents", icon: <img src={Images.documents} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
    { path: "/profile/applications", name: "My Products", icon: <img src={Images.products} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
    { path: "/profile/recommendedCourses/generate", name: "Recommendation", icon: <img src={Images.recommendations} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
    { path: "/profile/shortlisted", name: "Favourites", icon: <img src={Images.wishlist} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
    { path: "/profile/advisors", name: "Advisors", icon: <img src={Images.experts} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
    { path: "/profile/orders", name: "My Orders", icon: <img src={Images.orders} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem" }} /> },
    { path: "/profile/cart", name: "Cart", icon: <img src={Images.cart} alt="dashboard" style={{ width: "1.5rem", height: "1.5rem", }} /> },
    {
      name: "Logout",
      path: "",
      icon: <img src={Images.logout} alt="logout" style={{ width: "1.5rem", height: "1.5rem", }} />,
    },
  ];


  React.useEffect(() => {
    // console.log("storage updated");
  }, [exploreHistory]);

  const [open, setOpen] = React.useState(false);
  const [sidebarOpen, setSidebarOpen] = React.useState(false);
  const token = localStorage.getItem("_auth");
  const navigate = useNavigate();
  const userAuthStatus = useAppSelector((state) => state.userAuthStatus);
  const sharedInfo = useAppSelector((state) => state.sharedInfo);
  const [imgSrc, setImgSrc] = React.useState(sharedInfo.data?.displayPicSrc?sharedInfo.data?.displayPicSrc:Images.GroupUp);
  const dispatch = useAppDispatch();

  const getGeoInfo = async () => {
    try {
      const response = await axios.get("https://ipapi.co/json/");
      if (response) {
        let currency = response.data.currency;
        localStorage.setItem("currency", currency);
      }
    } catch (error) {
      // console.log(error);
    }
  };

  React.useEffect(() => {
    getGeoInfo();
  }, []);

  const logOut = async () => {
    setOpen(false)
    logout();
    if (token) {
      try {
        const response = await AuthLogout();
        if (response) {
          navigate("/")
        }
      } catch (err) { }
    }
  };

  const eventHandler = (event: Event) => {
    switch (event.name) {
      case "OpenUserMenu":
        setAnchorElUser(event.data);
        break;

      case "OpenNavMenu":
        setAnchorElNav(event.data);
        break;

      case "CloseNavMenu":
        // console.log(event.data)
        if (event.data.title === "Explore") {
          setAnchorElExplore(anchorElExplore ? null : event.data);
        } else {
          if (event.data.title === "Courses" || event.data.title === "Universities" || event.data.title === "Destinations") {
            const url = window.btoa(event.data.link)
            navigate(`/explore?${url}`)
          } else {
            navigate(event.data.link);
          }
          setAnchorElNav(null);
          setActivePage(event.data.title);
        }
        break;

      case "CloseUserMenu":
        setAnchorElUser(null);
        if (event.data.name === "Logout") {
          // logOut();
          setOpen(true)
        } else {
          setAnchorElUser(null);
          navigate(event.data.path);
        }
        break;

      case "login":
        setAnchorElNav(null);
        dispatch(
          setPopup({
            show: true,
            data: {
              container: {
                name: "login",
                dimensions: {
                  width: "800px",
                  height: "70%",
                  show: "none"

                },
              },
              type: "custom",
            },
          })
        );
        break;

      case "signup":
        setAnchorElNav(null);
        dispatch(
          setPopup({
            show: true,
            data: {
              container: {
                name: "signup",
                dimensions: {
                  width: "500px",
                },
              },
              type: "custom",
            },
          })
        );
        break;
    }
  };

  const listRef1 = React.useRef<HTMLUListElement | null>(null);
  const isProfileRoute = location.pathname.startsWith("/profile");
  const isPrivacyPolicyRoute = location.pathname === "/privacy-policy";

  React.useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (listRef1.current && !listRef1.current.contains(event.target as Node)) {
        setAnchorElExplore(null);
      }
    };

    if (anchorElExplore) {
      document.addEventListener("mousedown", handleClickOutside);
    } else {
      document.removeEventListener("mousedown", handleClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [anchorElExplore]);
  
const handleSingleClick = (e: React.MouseEvent<HTMLImageElement | HTMLButtonElement>) => {
  eventHandler({
    name: "OpenUserMenu",
    data: e.currentTarget,
  });
  setImgSrc(Images.GroupDown); // Use the correct image path as a string
};
  
// const handleDoubleClick = (e: React.MouseEvent<HTMLImageElement | HTMLButtonElement>) => {
//   setImgSrc(Images.GroupUp); // Use the correct image path as a string
// };


  

  return (
    <>
      <AppBar
        position="fixed"
        sx={{
          top: 0,
          "&.MuiAppBar-root": {
            backgroundColor: "#fff",
            px: { xs: 0, sm: 5 },
            boxShadow: "0",
          },
        }}
      >
        <Container sx={{
          "&.MuiContainer-root": {
            maxWidth: "1440px !important"
          }
        }}>
          <Toolbar
            disableGutters
            sx={{ justifyContent: "space-between", alignItems: "center" }}
          >
            <Stack direction="row" spacing={2} sx={{ alignItems: "center" }}>
              <Box
                component="img"
                src={isPrivacyPolicyRoute ? Images.campusRootLogo : Images.oneWindow}
                alt="logo"
                sx={{ width: "150px", mr: 1, cursor: "pointer" }}
                onClick={() => navigate("/")}
              />
              {!isPrivacyPolicyRoute && (
                <>
                  <Box
                    sx={{ position: "relative", display: { xs: "none", md: "flex" } }}
                  >
                    {pages.map((page: any) => (
                      <ListItemButton
                        key={page.title}
                        onClick={() => {
                          eventHandler({ name: "CloseNavMenu", data: page });
                        }}
                        sx={{
                          display: "block",
                          position: "relative",
                          color: ((activePage === page.title && location.pathname === page.link && !isProfileRoute) || (page.title === "Explore" && (anchorElExplore !== null || location.pathname.startsWith("/explore")))) ? "#70A6B2" : "#495052",
                          textTransform: "capitalize",
                          fontSize: "1rem",
                          fontWeight: 600,
                        }}
                      >
                        <Box sx={{ display: { xs: "none", md: "block" } }}>{page.title}</Box>
                      </ListItemButton>
                    ))}
                    {anchorElExplore && (
                      <>
                        <Backdrop
                          open
                          sx={expandCardStyles.backdropFilter}
                          onClick={() => setAnchorElExplore(null)}
                        />
                        <Box
                          sx={expandCardStyles.explorepop}
                        >
                          <List
                            sx={expandCardStyles.listRef}
                            ref={listRef1}
                          >
                            <Explorepopup setopen={setAnchorElExplore} />
                          </List>
                        </Box>
                      </>
                    )}
                  </Box>
                  <Box sx={{ alignItems: "center", display: { md: "flex", xs: "none" } }}>
                    <Search2/>
                  </Box>
                </>
              )}
            </Stack>

            <Stack direction="row" spacing={2} alignItems="center">
              <Box sx={{ flexGrow: 0 }}>
                {userAuthStatus.data?.isAuthorized ? (
                  !isProfileRoute ? (
                    <Box
                      sx={{
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                        height: "50px",
                        gap: "20px",
                      }}
                    >
                      <IconButton
                        onClick={handleSingleClick}
                        sx={{ p: 0 }}
                      >
                        <img
                          src={imgSrc}
                          alt="profile"
                          style={{ width: 40, height: 40, borderRadius: "50%", cursor: "pointer" }}
                        />
                      </IconButton>
                    </Box>
                  ) : (
                    <Box
                      sx={{
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                        gap: "12px",
                      }}
                    >
                      {/* Profile Icon for Mobile */}
                      <IconButton
                        onClick={() => setSidebarOpen(true)}
                        sx={{
                          bgcolor: '#fff',
                          borderRadius: '50%',
                          boxShadow: 2,
                          width: 40,
                          height: 40,
                          display: { xs: 'flex', md: 'none' }, // Only show on mobile
                          alignItems: 'center',
                          justifyContent: 'center',
                          border: '2px solid #E6E6E6',
                          '&:hover': {
                            bgcolor: '#f5f5f5',
                          },
                        }}
                      >
                        <img
                          src={imgSrc}
                          alt="profile"
                          style={{ width: 28, height: 28, borderRadius: "50%" }}
                        />
                      </IconButton>
                      <CustomButton handleSubmit={() => setOpen(true)} width="100px">
                        Logout
                      </CustomButton>
                    </Box>
                  )
                ) : (
                  !isPrivacyPolicyRoute && (
                    <Box sx={{ display: { xs: "none", md: "flex" }, flexDirection: "row", gap: "1rem", px: '10px' }} >
                      <Button
                        onClick={() => eventHandler({ name: "login" })}
                        sx={[globalButton, { fontSize: '15px', px: "15px" ,background:"#70A6B2","&:hover": {
                        backgroundColor: "#70A6B2",
                        color: "#fff",
                        }, }]}
                      >
                        Sign In
                      </Button>
                    </Box>
                  )
                )}
                <Menu
                  sx={{ mt: "45px"}}
                  id="menu-appbar"
                  anchorEl={anchorElUser}
                  anchorOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  open={Boolean(anchorElUser)}
                  onClose={() => {
                    setImgSrc(sharedInfo?.data?.displayPicSrc || "");
                    eventHandler({ name: "CloseUserMenu", data: "" });
                  }}
                  PaperProps={{
                    sx: {
                      borderRadius: '20px', 
                      minWidth: 220,         
                    }
                  }}
                >
                  <Box
                    sx={{
                      width: '80%',
                      bgcolor: '#F5F6FA',
                      borderRadius: '15px',
                      p: 1.5,
                      mb: 2,
                      mt: 1,
                      mx:1,
                      display: 'flex',
                      alignItems: 'center',
                      gap: 1
                    }}
                  >
                    <Avatar
                      alt="profile"
                      src={sharedInfo?.data?.displayPicSrc || Images.GroupUp}
                      sx={{ width: 40, height: 40, mr: 1 }}
                    />
                    <Typography
                      sx={{
                        fontWeight: 700,
                        fontSize: 16,
                        color: '#3B3F76',
                        whiteSpace: 'nowrap',
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        fontFamily: 'Inter, sans-serif'
                      }}
                    >
                      {sharedInfo?.data?.firstName}
                    </Typography>
                  </Box>
                  {settings.map((setting: any) => (
                    <MenuItem
                      key={setting.name}
                      onClick={() => {
                        setImgSrc(sharedInfo?.data?.displayPicSrc || "");
                        eventHandler({ name: "CloseUserMenu", data: setting });
                      }}
                      sx={{
                        display: "flex",
                        gap: "5px",
                        marginLeft: "5px",
                        "&:hover": {
                          background: "#d8d8e9",
                          borderRadius: "10px",
                        },
                        minHeight: "40px",
                      }}
                    >
                      {setting.icon}
                      <Typography
                        textAlign="center"
                        color="#3b3f7691"
                        sx={{
                          fontWeight: "500",
                          "&:hover": {
                            color: "#3b3f76",
                          },
                        }}
                      >
                        {setting.name}
                      </Typography>
                    </MenuItem>
                  ))}
                </Menu>
              </Box>
              <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
                <IconButton
                  size="large"
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  aria-haspopup="true"
                  onClick={(e) => {
                    eventHandler({
                      name: "OpenNavMenu",
                      data: e.currentTarget,
                    });
                  }}
                >
                  <MenuIcon/>
                </IconButton>

                <Menu
                  id="menu-appbar"
                  anchorEl={anchorElNav}
                  anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "left",
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: "top",
                    horizontal: "left",
                  }}
                  PaperProps={{
                    style: {
                      left: "0 !important"
                    },
                  }}
                  open={Boolean(anchorElNav)}
                  onClose={() => setAnchorElNav(null)}
                  sx={{
                    display: { xs: "block", md: "none" },
                    width: "100%",
                    "& .MuiPaper-root": {
                      width: "100%"
                    },
                    "& .MuiList-root": {
                      // background: "#1C1F40",
                      p: 2,
                      height: "80vh"
                    },
                  }}
                >
                  <Box sx={{ display: { md: "none", xs: "flex" }, mb: 2 }} >
                    <Search2/>
                    {/* <Search
                      appliedFilter={props.appliedFilter}
                      setAppliedFilter={props.setAppliedFilter}
                      setAnchorElNav={setAnchorElNav}
                    /> */}
                  </Box>
                  <Stack direction="column">
                    {pages.map((page, index) => (
                      <React.Fragment key={page.title}>
                        <MenuItem
                          onClick={() => {
                            if (page.options) {
                              setAnchorElNav(null);
                              setTimeout(() => setExploreOpen(true), 100);
                            } else {
                              eventHandler({ name: "CloseNavMenu", data: page });
                            }
                          }}
                          sx={{ minHeight: "35px", display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}
                        >
                          <Typography textAlign="center" color="#3b3f76">{page.title}</Typography>
                          {page.options && <KeyboardArrowDownIcon sx={{ color: "#3b3f76" }}/>}
                        </MenuItem>
                      </React.Fragment>
                    ))}
                    {!userAuthStatus.data?.isAuthorized ? (
                      <Stack direction="column" sx={{ display: { xs: "flex", lg: "none" }, justifyContent: "center", alignItems: "center", mt: 2 }} spacing={2}>
                        <Button
                          onClick={() => eventHandler({ name: "login" })}
                          sx={[globalButton,{
                            
                            border: "2px solid #70A6B2",
                            color: "#fff",
                            fontWeight: "600",
                            fontSize: "16px",
                            textTransform: "none",
                            width: "150px",
                            padding: "2px",
                            borderRadius: "20px",
                            background: "#70A6B2"
                          }]}
                        >
                          <Person2Icon sx={{ mr: 0.5, fontSize: "1.4rem", color: "#fff" }} />
                          Sign In
                        </Button>
                      </Stack>
                    ) : ""}
                  </Stack>
                </Menu>
              </Box>
            </Stack>
          </Toolbar>

        </Container>
        {
          exploreopen && <ExplorePop exploreopen={exploreopen} setExploreOpen={setExploreOpen} setOpen={setAnchorElNav}/>}
        <ConfirmDialog
          open={open}
          handleClose={() => setOpen(false)}
          additionalData={{
            onSubmit: logOut,
            title: "Are you sure you want to logout?",
            content: "",
          }}
        />
        {/* Mobile Profile Sidebar Drawer */}
        <Drawer
          anchor="left"
          open={sidebarOpen}
          onClose={() => setSidebarOpen(false)}
          PaperProps={{
            sx: {
              width: 280,
              bgcolor: 'transparent',
              boxShadow: 0,
              border: 'none',
              p: 0,
            },
          }}
        >
          <ProfileSidebar forDrawer={true} onClose={() => setSidebarOpen(false)} />
        </Drawer>
      </AppBar>
    </>
  );
};

export default Header;
