import React, { useEffect, useState } from "react";
import { Dialog, Grid, Box, Typography, Button, List, ListItemButton, ListItemText, Collapse, Skeleton } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import { AllDestinationTypeEnum } from "src/assets/enums";
import { globalButton, testReadyStyles } from "../styles/customStyles";
import Images from "src/assets";
import { disciplineList } from "src/assets/menu";
import { useNavigate } from "react-router-dom";
import { useAppSelector } from "src/assets/hooks";
import { getAlllistings } from "src/services";
import { getExploreUrl } from "src/assets/library";

function ExplorePop(props: any) {
  const [activeTab, setActiveTab] = useState<"Universities" | "Programs">("Programs");
  const [activeCountry, setActiveCountry] = useState<string>("United States of America");

  const Courses = ({ setopen, setmenu, activeCountry }: any) => {
    const navigate = useNavigate();
    return (
      <>
        <Box sx={{ position: 'relative' }}>
          <Box sx={{ position: 'relative', pt: 1 }}>
            <Box sx={{
              padding: '1rem', boxShadow: "0px 0px 4px 0px #00000040", borderRadius: "25px"
            }}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <div>
                    <Typography color="#00000080" fontWeight="500" fontSize="1.2rem" sx={{ pl: 2 }}>
                      TOP DISCIPLINES
                    </Typography>
                  </div>
                  <List sx={{ width: '100%', maxWidth: 360, height: "200px", overflowY: "scroll", scrollbarWidth: "none", "& .MuiTypography-root": { fontSize: "14px" } }} component="nav">
                    {(disciplineList || []).map((discipline: any) => (
                      <ListItemButton 
                        key={discipline.name} 
                        onClick={() => {
                          if (setopen) setopen(false);
                          if (setmenu) setmenu(null);
                          navigate(getExploreUrl("filterData", [{ type: "subDiscipline", data: [{ name: discipline.name, value: discipline.name }] }]));
                        }} 
                        sx={{ py: 0, pr: -4, display: 'flex', justifyContent: 'space-between' }}
                      >
                        <ListItemText primary={discipline.name} />
                        <img src={Images.arrow} alt="arrow" style={{ width: "20px" }} />
                      </ListItemButton>
                    ))}
                  </List>
                </Grid>
              </Grid>
            </Box>
            <Box sx={{ textAlign: "center", mt: 2 }}>
              <Button
                sx={[globalButton, {
                  width: "80px", fontSize: "14px"
                }]}
                onClick={() => {
                  if (setopen) setopen(false);
                  if (setmenu) setmenu(null);
                  navigate(getExploreUrl("filterData", [{ type: "country", data: [{ name: activeCountry, value: activeCountry }] }]));
                }}
              >
                View all
              </Button>
            </Box>
          </Box>
        </Box>
      </>
    )
  }

  const Universities = ({ setopen, setmenu, activeCountry }: any) => {
    const navigate = useNavigate();
    const preference = useAppSelector((state) => state.preferences);
    const currency = localStorage.getItem("currency");
    const [loading, setLoading] = useState(true);
    const [universityData, setuniversityData] = useState([]);

    const universities = async (activeTab: string) => {
      setLoading(true);
      const response = await getAlllistings("universities", {
        page: 1,
        currency: preference.data?.currency
          ? preference.data.currency
          : currency
            ? currency
            : "INR",
        filterData: [{ type: "country", data: [activeTab] }],
      });
      if (response) {
        setuniversityData(response.data.list);
      }
      setLoading(false);
    };

    useEffect(() => {
      universities(activeCountry);
      // eslint-disable-next-line
    }, [activeCountry]);

    return (
      <div style={{}}>
        <Box sx={{ position: 'relative', padding: { xl: "1rem 7rem 0 7rem", xs: "1rem" } }}>
          <Box sx={{
            padding: '1rem', boxShadow: "0px 0px 4px 0px #00000040", borderRadius: "25px"
          }}>
            <Grid container spacing={1} >
              <Grid item xs={12}>
                <div>
                  <Typography color="#00000080" fontWeight="500" fontSize="1.2rem" sx={{ pl: 2 }}>
                    TOP UNIVERSITIES
                  </Typography>
                </div>
              </Grid>
              <Grid item xs={12}>
                <Grid container spacing={1}>
                  {loading ? (
                    <Grid item xs={12} md={6}>
                      {[...Array(6)].map((_, i) => (
                        <Skeleton key={i} height={30} sx={{ mb: 1, bgcolor: '#f5f2f2' }} />
                      ))}
                    </Grid>
                  ) :
                  (
                    (universityData || []).slice(0, 9).map((item: any, i) => (
                      <Grid item xs={12} md={6} sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                        <Typography
                          color="#000"
                          key={i}
                          sx={{
                            display: "flex", gap: "0.5rem", alignItems: "center", padding: "4px 0", fontSize: "14px", width: "260px"
                          }}
                        >
                          <img src={item?.logoSrc} alt="" style={{ width: "1.5rem", height: "1.5rem", borderRadius: "50%" }} />
                          {item?.name}
                        </Typography>
                        <Box component="img" src={Images.arrow} alt="arrow" onClick={() => {
                          if (setopen) setopen(false);
                          if (setmenu) setmenu(null);
                          navigate(`/singleuniversity/${item?._id}`);
                        }} sx={{ width: { md: "22px", xs: "20px" }, height: { md: "22px", xs: "20px" }, marginRight: "10px", cursor: "pointer" }} />
                      </Grid>
                    ))
                  )}
                </Grid>
              </Grid>
            </Grid>
          </Box>
          <Box sx={{ textAlign: "center", mt: 2 }}>
            <Button
              sx={[globalButton, {
                width: { md: "100px", xs: "80px" },
              }]}
              onClick={() => {
                if (setopen) setopen(false);
                if (setmenu) setmenu(null);
                const subDisUrl = `activeTab=${JSON.stringify(
                  "universities"
                )}&FilterData=${JSON.stringify([
                  {
                    type: "country",
                    data: [{ name: activeCountry, value: activeCountry }],
                  },
                ])}&page=${JSON.stringify(1)}&search=${JSON.stringify("")}`
                const subDisciUrl = window.btoa(subDisUrl);
                navigate(`/explore?${subDisciUrl}`);
              }}
            >
              View all
            </Button>
          </Box>
          <Box component="img" src={Images.flight} alt="flight" sx={{ width: "50px", position: "absolute", top: 50, right: 5, display: { xs: "none", md: "block" } }} />
        </Box>
      </div>
    );
  };

  const tabs = [
    {
      label: "Courses",
      Component: <Courses setopen={props.setExploreOpen} setmenu={props.setOpen} activeCountry={activeCountry} />,
      link: `/explore?activeTab=${JSON.stringify("courses")}&FilterData=${JSON.stringify([])}&page=${JSON.stringify(1)}&search=${JSON.stringify("")}`,
    },
    {
      label: "Universities",
      Component: <Universities setopen={props.setExploreOpen} setmenu={props.setOpen} activeCountry={activeCountry} />,
      link: `/explore?activeTab=${JSON.stringify("universities")}&FilterData=${JSON.stringify([])}&page=${JSON.stringify(1)}&search=${JSON.stringify("")}`,
    },
  ];

  return (
    <div >
      <Dialog open={props.exploreopen} fullWidth maxWidth="xs" PaperProps={{
        sx: {
          padding: "16px",
          borderRadius: "20px",
          margin: "16px",
          maxHeight: 'unset',
          overflowY: 'unset',
          "& .MuiModal-root":{
            zIndex:0
          }
        }
      }}>
        <div style={{ position: "relative" }}>
        <Box sx={{ position: 'absolute', top: -5, right: 0, zIndex: 2 }}>
            <CloseIcon sx={{ color: "#3b3f76", cursor: "pointer" }} onClick={() => {
              if (props.setExploreOpen) props.setExploreOpen(false);
              if (props.setOpen) props.setOpen(null);
            }} />
          </Box>
          <Grid container spacing={2} sx={{ marginTop: "-8px" }}>
            <Grid
              item
              xs={12}
              sx={{ display: "flex", gap: "1rem", px: 3 }}
            >
              {tabs.map((item: any) => (
                <Button
                  onClick={() => {
                    setActiveCountry("United States of America")
                    setActiveTab(item.label)
                  }
                  }
                  sx={[testReadyStyles.activeButton, {
                    background: item.label === activeTab ? " #3b3f76" : "#3b3f7677",
                    color: "#fff",
                    borderRadius: "10px",
                    "&:hover": {
                      background: "#3b3f76"
                    }
                  }]}
                >
                  {item.label}
                </Button>
              ))}
            </Grid>
            <Grid
              item
              xs={12}
              sx={{}}
            >
              {tabs.find((item) => item.label === activeTab)?.Component}

              <Box
                display="flex"
                flexWrap="nowrap"
                sx={{
                  gap: "1rem",
                  mt: 2,
                  overflowX: "scroll",
                  scrollbarWidth: "none"
                }}
              >
                {Object.entries(AllDestinationTypeEnum).map(([title, value]) => (
                  <Button
                    key={value}
                    onClick={() => setActiveCountry(value)}
                    sx={[testReadyStyles.exporeButton, {
                      border: value !== activeCountry ? "1px solid #3b3f76" : "2px solid var(--3-primary-teal, #75ECE5)",
                      color: value === activeCountry ? "#000" : "#3b3f76",
                      background: value === activeCountry ? "#9CFFF980" : "#fff",
                    }]}
                  >
                    {title}
                  </Button>
                ))}
              </Box>
            </Grid>
          </Grid>
        </div>
      </Dialog>
    </div>
  );
}

const styles = {
  header: {
    fontWeight: 500,
    fontSize: "18px",
    color: "#3b3f76",
  },
  buttonsContainer: {
    display: "flex",
    gap: "10px",
    flexWrap: "wrap",
  },
  buttonText: {
    background: "#3b3f76",
    color: "white",
    padding: "10px",
    borderRadius: "10px",
    fontWeight: 500,
    fontSize: "14px",
    textAlign: "center",
  },
  gridContainer: {
    display: "flex",
    flexWrap: "nowrap",
    border: "1px solid grey",
    borderRadius: "10px",
    margin: "20px",
    textAlign: "left",
    padding: "10px",
    boxShadow: "none",
    width: "auto"
  },
  gridItem: {
    padding: "10px",
  },
  sectionHeader: {
    fontWeight: 600,
    fontSize: "12px",
    lineHeight: "15px",
    color: "rgba(106, 106, 106, 1)",
  },
  itemText: {
    fontWeight: 600,
    fontSize: "12px",
    lineHeight: "15px",
  },
};

export default ExplorePop;